
.backgrounduniversity{
  background-image: linear-gradient(rgba(6, 48, 83, 0.7), rgba(6, 48, 83, 0.7)), url(./../../assets/png/universityBackground.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 240px;
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}
.card {
  width: 310px;
  height: auto;
  border-radius: 20px;
  border: 1px solid #ccc;
  cursor: pointer;
  box-shadow: 0 0 10px gray;
  transition: 0.3s ease;
}

.card:hover {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: .5s;
  border-radius: 20px;
}

  .cardImageContainer::before {
    content: "";
    border-radius: 0 0 20px 20px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 20%), rgba(0, 0, 0, 20%));
    z-index: 2;
  }
.face {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
  .back {
    background-color: #fff;
    z-index: 2;
    padding: 30px 20px;
    border-radius: 20px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
  }

.card:hover .back,
.card.clicked .back { 
  opacity: 1;
  visibility: visible;
}
@media(max-width: 600px){
  .card {
    width: 250px;
    height: 100%;
  }

}
@media(max-width: 450px){
  .backgrounduniversity{
  height: 180px;
  }
}